html {
    --main-bg: #F2F2F2;
    --second-bg: #d9d9d9;
    --blue-main: #5EB7FF;
    --blue-dark: #4280B3;
    --orange-main: #FFC05E;
    --orange-dark: #B3781E;
    background-color: var(--main-bg);
    font-family: monospace;
    color: black;
    font-size: 16px;
}

body {
    margin: 0;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

a {
    text-decoration: none;
}

a:link, a:visited {
    color: var(--blue-dark);
}

a:hover, a:focus {
    border-bottom: 1px solid;
    color: white;
    background: var(--blue-dark);
}

a:active {
    color: black;
    background: var(--blue-dark);
}

.main-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
}
.presentation {
    display: block;
    text-align: justify;
}
.main-panel {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 60px 0 60px;
    border-left: 6px solid #B3B3B3;
}
.main-wrap {
    flex: 1;
    width: 800px;
}
.side-panel {
    background: var(--second-bg);
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-right: 30px;
}
.header-wrap {
    width: 340px;
    display: flex;
    flex-direction: column;
}

.header-wrap header {
    display: flex;
    flex-direction: column;
}

.work-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 20px;
    margin: 1rem 0;
}

.card {
    border: solid 1px black;
    display: grid;
    grid-template-rows: 200px max-content 1fr;
    background-color: black;
    box-shadow: 8px 8px #b3b3b3;
}

.card .img-div {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    height: 100%;
    width: 100%;
}

.card header {
    color: white;
    border-bottom: 2px solid var(--orange-main);
    padding: 0 0.5rem 0.5rem;
    font-size: 1rem;
}

.card div.description {
    background: var(--second-bg);
    padding: 0.5rem;
    border-bottom: 2px solid black;
}

.card div.footer {
    color: white;
    padding: 0.3rem;
    text-align: end;
}

.card div.footer > a:link, .card div.footer > a:visited {
    outline: 3px solid var(--orange-main);
    background: var(--orange-main);
    color: black;
}

.card div.footer > a:hover, .card div.footer > a:focus {
    border-bottom: 1px solid;
    color: black;
    background: var(--orange-dark);
    outline: 3px solid var(--orange-dark);
}

.card div.footer > a:active {
    color: white;
    background: var(--orange-dark);
    outline: 3px solid var(--orange-dark);
}

.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px auto;
    height: 240px;
}

svg.logo-crt {
    width: 100%;
    height: 100%;
}

nav > ul {
    text-align: end;
    list-style: none;
    font-size: 1.25rem;
    line-height: 2.2;
}

nav > ul > li > a {
    background-color: white;
    outline: 5px solid white;
}

nav > ul > li > a:link, nav > ul > li > a:visited{
    color: black;
}

nav > ul > li > a:focus {
    border-bottom: 1px solid;
    background: var(--blue-dark);
    outline: 5px solid var(--blue-dark);
    color: white;
}

nav > ul > li > a:hover {
    border-bottom: 1px solid;
    background: var(--blue-main);
    outline: 5px solid var(--blue-main);
    color: white;
}

nav > ul > li > a:active {
    background: var(--blue-main);
    outline: 5px solid var(--blue-main);
    color: black;
}

.contact-bar {
    display: flex;
    height: 45px;
    justify-content: flex-end;

}

.contact-bar > a > img.logo {
    height: 100%;
    width: auto;
}

.div-list {
    display: flex;
    flex-direction: column;
}

.div-list > div {
    margin: 0.5rem 0 0 1rem;
    font-size: 1.1rem;
}

.fc-name {
    font-size: 2rem;
    font-weight: 800;
    text-align: end;
    margin-top: 1rem;
}

.fc-display-1 {
    text-decoration: underline;
    font-size: 2.5rem;
    font-weight: 700;
    margin-top: 1rem;
}

.fc-display-2 {
    text-decoration: underline;
    font-size: 2rem;
    font-weight: 600;
    margin-top: 1rem;
}

.fc-par {
    margin-top: 0.5rem;
    font-size: 1.2rem;
}
